@import url(https://fonts.googleapis.com/css?family=Oswald|Roboto+Condensed&display=swap);
body{background-color:black;margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}p{font-family:"Roboto Condensed"}

.MyNavbar{display:block}.navbar{box-shadow:inset 4px 4px 15px rgba(255,255,255,0.1),inset -4px -4px 15px rgba(255,255,255,0.1)}

.CounterRow{display:flex;width:100%;flex-direction:column}.borderSpace{margin-right:15px;padding-right:15px}.countersRow{display:flex;flex-flow:row wrap;align-items:center;padding-top:5px;padding-bottom:5px;box-shadow:inset 4px 4px 15px rgba(255,255,255,0.1),inset -4px -4px 15px rgba(255,255,255,0.1)}.counterCard{box-shadow:none}.counterMiddleBorder{width:15px;padding:0 15px;margin:0 15px}.countersDetails{font-size:.85rem}.countersDetails p{margin:8px 0}.hardCounter{border:2px solid #e44747}.insideCountersRow{display:flex;flex-flow:row wrap}.softCounter{border:2px solid #9B47E4}.opponentImg{border:2px solid gray}.teamName{font-weight:400;font-size:.8rem;margin:0}.toonImg{width:35px;height:35px;border-radius:50%;margin:0 2.5px}@media only screen and (min-width: 768px){.toonImg{width:45px;height:45px}.countersDetails{font-size:.9rem}}@media only screen and (min-width: 992px){.toonImg{width:55px;height:55px}.countersDetails{font-size:.95rem}}@media only screen and (min-width: 1200px){.toonImg{width:65px;height:65px}.countersDetails{font-size:1rem}}

.Counters3v3{display:flex;flex-direction:column;text-align:center}.columnTitles{display:flex;flex-direction:row;margin-top:10px}.columnTitles h1{font-size:1.7rem}.columnTeams{display:flex;flex-direction:column}.hardCounterColorBox{display:inline-block;height:20px;width:20px;background-color:#e44747;border-radius:100px;margin-right:10px}.patreonBtn{background:rgba(255,0,0,0.863);color:whitesmoke;text-align:center;align-items:center;justify-content:center;margin:0;font-weight:500;font-size:1.25em}.softCounterColorBox{display:inline-block;height:20px;width:20px;background-color:#9B47E4;border-radius:100px;margin-right:10px}@media only screen and (min-width: 768px){.columnTitles h1{font-size:2rem}}

.Counters5v5{display:flex;flex-direction:column;text-align:center}.columnTitles{display:flex;flex-direction:row;margin-top:10px}.columnTitles h1{font-size:1.7rem}.columnTeams{display:flex;flex-direction:column}.hardCounterColorBox{display:inline-block;height:20px;width:20px;background-color:#e44747;border-radius:100px;margin-right:10px}.patreonBtn{background:rgba(255,0,0,0.863);color:whitesmoke;text-align:center;align-items:center;justify-content:center;margin:0;font-weight:500;font-size:1.25em}.softCounterColorBox{display:inline-block;height:20px;width:20px;background-color:#9B47E4;border-radius:100px;margin-right:10px}@media only screen and (min-width: 768px){.columnTitles h1{font-size:2rem}}

.App{display:block;background-color:black;box-shadow:inset 0px 0px 10px rgba(255,255,255,0.1),inset 0px 0px -10px rgba(255,255,255,0.1);font-family:'Oswald', sans-serif;color:whitesmoke}

